*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,li{list-style: none;}
a{
  text-decoration: none;
  color: #444;
}
.header_wrap{border-bottom: 1px solid #ddd;}
.header_inner{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1500px;
  height: 100px;
  margin: auto;
}
.main{
  width: 1500px;
  display: grid;
  grid-template-columns: 48% 48%;
  grid-template-areas: 
  'visual tabs'
  'visual ref';
  margin: 2rem auto;
  grid-gap: 2rem;
}
.main_visual{grid-area: visual;}
.main_visual img{
  width: 100%;
  height: 100%;
  border-radius: 18px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
}
.tabs{
  grid-area: tabs;
  border-radius: 18px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
}
.tabs button{
  background-color: #fff;
  padding :1rem 2rem;
  border: none;
  font-size: 28px;
  font-weight: 600;
  color: #222;
  cursor: pointer;
}
button.active {
  border-bottom: 3px solid #4a47f3;
  font-weight: bold;
}
.tabs .button_tab{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.sub_con{
  padding: 2rem;
}
.main_ref{
  grid-area: ref;
  border-radius: 18px;
  padding: 2rem;
  width: 100%;
  margin: auto;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
}
.main_ref p{
  margin-top: 1rem;
  color: #6a6a6a;
}
.main_ref::before{
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: lightblue;
  border-radius: 100%;
  top: -30%;
  right: 10%;
}